import styled from 'styled-components';
import { colors } from '../colors';

export const TopContainer = styled.div`
  width: 100%;
  height: 100%;
  background-color: ${colors.darkPurple};
  display: inline-block;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;