import React from 'react';
import { Container, ContainerInner, SpeechBubble, Text } from './styles.js';
import LoadingDots from './LoadingDots';

class Chatbox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            messages: [
               {message: `Velkommen til quizen! Hva heter favoritt artisten til Daniel? (Hint: ikke Kygo)`,
                user: 'Helena' },
            ],
            textField: '',
            typing: false,
            index: 0,
            tries: 3,
            score: 0
        };
      }

    scrollToBottom() {
        const scrollHeight = this.messageList.scrollHeight;
        const height = this.messageList.clientHeight;
        const maxScrollTop = scrollHeight - height;
        this.messageList.scrollTop = maxScrollTop > 0 ? maxScrollTop : 0;
    }

    componentDidUpdate() {
        this.scrollToBottom();
    }
    getWrong(){
        if(this.state.tries >= 1){
            return `Feil! du har ${this.state.tries} fors칮k igjen`;
        }else {
            return `Feil! du er tom for fors칮k`;
        }
    }

    response = [
        {
            correct: `Han digger 친 h칮re p친 sin egen musikk 游땢,  
            Neste sp칮rsm친l: hva er favoritt drikken til Daniel?`,
            message: `Riktig svar er Cola s친klart. Lett sp칮rsm친l siden forrige var s친 vanskelig: hva er favoritt drikken til Daniel?`,
            answer: 'naden',
        },
        {
            correct: `Riktig! Det er Cooolaaaa! Det var en natt der Daniel v친knet med intens krampe i foten og s칮kte opp hva det var symptom p친 游, hva fant Daniel ut at han var?`,
            message: `Svaret er Naden Han digger 친 h칮re p친 sin egen musikk 游땢: Hva er favoritt drikken til Daniel?`,
            answer: 'cola zero',
        },
        {
            correct: `Riktig! Han er gravid i 26 m친ned, babyen m친 jo komme snart? Hvor mange fingre har Daniel?`,
            message: `Feil Daniel sin favoritt drikk er Cola Zero, er det s친 rart? n친r den er bevist til 친 kurere alt av sykdommer 游때. Hvor mange fingre har Daniel p친 kroppen sin?`,
            answer: 'gravid',
        },
        {
            correct: `Riktig! Daniel har 10 fingre p친 kroppen sin, problemet er at de er s친 lange at de kunne like gjerne v칝rt 20 fingre 九껊잺 Hvor var Daniel og Helena p친 ferie i sommer?`,
            message: `Daniel har 10 fingre p친 kroppen sin, problemet er at de er s친 lange at de kunne like gjerne v칝rt 20 fingre. Hvor var Daniel og Helena p친 ferie i sommer?`,
            answer: '10',
        },
        {
            correct: `Riktig! De var p친 ferie i Italia. Hvilken jente synest han er den vakreste i verden?`,
            message: `De var p친 ferie i Italia, Bonjour!. Hvilken jente synest han er den vakreste i verden?`,
            answer: 'italia',
        },
        {
            correct: `Det er meg! Jeg vet!! 游땘游땘游땘`,
            message: `Riktig svar er Helena Felix De Melo s친klart, har du sett det smilet? Oh my god 游땘`,
            answer: 'helena',
        },
    ];

    handleKeyDown = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            if(this.state.index < this.response.length){

                if(this.state.textField.length > 0){
                    var text = this.state.textField;
                    this.setState({textField: ''});
                this.setState({typing: true});
                this.setState(prevState => ({
                    messages: [...prevState.messages, {message: text, user: 'Daniel'}],
                    typing: true
                  }))
                  if(this.state.tries > 0) {
                      if(this.response[this.state.index].answer.indexOf(text.trim().toLowerCase()) !== -1){
                          setTimeout(
                              function() {
                                  this.setState(prevState => ({
                                      messages: [...prevState.messages, {message: this.response[this.state.index].correct, user: 'Helena'}],
                                    typing: false,
                                    index: this.state.index + 1,
                                    tries: 3,
                                    score: this.state.score + 1,
                                }))
                            }
                            .bind(this),
                            100 + this.response[this.state.index].correct.length*20
                            );
                        }else {
                            setTimeout(
                                function() {
                                    this.setState(prevState => ({
                                        messages: [...prevState.messages, {message: this.getWrong(), user: 'Helena'}],
                                        typing: false,
                                        tries: this.state.tries - 1,
                                    }))
                                }
                                .bind(this),
                                100 + this.getWrong().length*10
                                );
                            }
                        }else if (this.state.tries === 0) {
                            setTimeout(
                                function() {
                                    this.setState(prevState => ({
                                    messages: [...prevState.messages, {message: this.response[this.state.index+1].message, user: 'Helena'}],
                                    typing: false,
                                    index: this.state.index + 1,
                                    tries: 3,
                                    score: this.state.score,
                                }))
                            }
                            .bind(this),
                            100 + this.response[this.state.index].correct.length*10
                            );
                        }
                    }
                    }
                }
            }
            
            handleChange = (e) => {
                this.setState({textField: e.target.value});
    }
    render() {
        return (
        <>
        {this.state.index === this.response.length ?
        <h2>Score: {this.state.score} / {this.response.length}</h2>
        :
        null
        }
        {this.state.index === this.response.length ? 
            <h1>Gratulerer du fikk en score p친 {this.state.score} av {this.response.length} mulige</h1>
            :
            null
        }
        <Container>
            <ContainerInner ref={(div) => {
                this.messageList = div;
            }}>
                {this.state.messages.map((item,i) => 
                    (item.user === 'Helena') ? 
                    <SpeechBubble left key={i}>{item.message}</SpeechBubble>
                    :
                    <SpeechBubble right key={i}>{item.message}</SpeechBubble>
                    )
                }
                {this.state.typing && <LoadingDots/>}
            </ContainerInner>
            <Text onKeyDown={this.handleKeyDown} onChange={this.handleChange} value={this.state.textField} multiline placeholder={'Skriv svaret ditt her'}/>
        </Container>
        </>
        )
    }
}
export default Chatbox;