import styled from 'styled-components';
import { colors } from '../colors';

export const Container = styled.div`
    width: 100%;
    max-width: 450px;
    height: 70%;
    display: inline-block;
    background-color: ${colors.deepBlue};
    border-radius: 3px;
    padding: 25px;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
`;

export const ContainerInner = styled.div`
    display: inline-block;
    background-color: ${colors.deepBlue};
    color:  ${colors.white};
    border-radius: 3px;
    padding: 15px;
    padding-right: 30px;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    position: relative;
`;

export const SpeechBubble = styled.div`
    display: block;
    background-color: ${props => (props.left? colors.lightPurple : colors.lightBlue)};
    color:  ${props => (props.left? colors.fadedPurple : colors.fadedBlue)};
    font-weight: 600;
    font-size: 14px;
    border-radius: 5px;
    padding: 12px;
    min-width: 200px;
    max-width: 300px;
    min-height: 40px;
    position: relative;
    margin-top: 30px;
    float: ${props => (props.right ? 'right' : 'left')};
    clear: both;
    &:before {
        content: "";
        position: absolute;
        bottom: 0px;
        left: -8px;
        opacity: ${props => (props.left ? 1 : 0)};
        width: 0; 
        height: 0; 
        display: inline-block;
        border-right: 15px solid ${props => (props.left? colors.lightPurple : colors.lightBlue)};
        border-top: 20px solid transparent;
    }
    &:after {
        content: "";
        position: absolute;
        bottom: 0px;
        right: -8px;
        opacity: ${props => (props.right ? 1 : 0)};
        width: 0; 
        height: 0; 
        display: inline-block;
        border-left: 15px solid ${props => (props.left? colors.lightPurple : colors.lightBlue)};
        border-top: 20px solid transparent;
    }
    & span {
        font-size: 10px;
    }
`;

export const Text = styled.textarea`
    background-color: ${colors.deepBlue};
    border: 2px solid ${colors.purple};
    color:  ${colors.white};
    padding: 15px;
    display: block;
    resize: none;
    margin-right: 30px;
    border-radius: 3px;
    &:focus {
        outline: none;
    }
`;